<template>
    <div class="coupon-wrapper" v-show="title">
        <div class="notice" v-if="status === 3 || status === 5">
            <span class="notice-text">活动结束了，{{timeCount}}s后自动跳转至首页。</span>
            <span class="notice-link" @click="eventNotice">点击跳转</span>
        </div>

        <div class="banner">
            <img v-lazy="banner" alt="" class="banner-img"/>
        </div>

        <div class="content">
            <div class="content-msg">{{statusEnum}}</div>
            <div class="coupons" v-if="prizes && showCoupon">
                <coupon-item v-for="(coupon, c) in prizes" :key="c" :coupon="coupon" :bottom="15"/>
            </div>
            <div class="coupon-btn" @click="eventGoBtn" v-if="btn_text && showCoupon">{{btn_text}}</div>
        </div>

        <div class="rule m-top20" v-if="description">
            <div class="rule-title">
                <span>活动规则</span>
            </div>
            <div class="rule-content">
                <div class="rule-part" v-for="(rule, r) in description" :key="r">
                    <span>{{r+1}}.</span>
                    <p>{{rule}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import share from '@/mixins/share'
    import API from '@/server/api'
    import {platform} from '@/utils/platform'
    import CouponItem from '@/components/CouponItem'

    export default {
        name: 'coupon',
        mixins: [share],
        components: {
            CouponItem
        },
        data() {
            return {
                //领券活动信息
                code: '',
                status: null,
                title: '',
                banner: '',
                btn_text: '',
                btn_url: '',
                native_url: '',
                description: '',
                prizes: null,
                id: '',
                time: null,     //计时器
                timeCount: 5,    //计时器初始时间
                attend_type: 0,
            }
        },
        computed: {
            statusEnum() {
                let i = this.status;
                let attend_type = this.attend_type;
                return {
                    '0': '你来早啦，活动还没有开始哦',    //新建活动
                    '1': '你来早啦，活动还没有开始哦',    //未开始
                    '2': '领取中...',
                    '3': '啊哦，活动已经结束啦',
                    '4': '今天活动还没开始哦，再等等',    //当天活动未开始
                    '5': '啊哦，今天已经结束啦，明天早点来吧',     //当天活动已结束且尚有明日活动
                    '6': '今天的优惠券已经被抢光了，明天再来吧',   //今日优惠券被领光且明天可领
                    '7': '好券不等人，优惠券已经被抢光啦',        //券总量已被领完
                    '97': `你${attend_type?'今天':''}已经领过优惠券啦`,  //已领过
                    '98': '抱歉，本活动仅限老用户参与',
                    '99': '抱歉，本活动仅限新用户参与',
                    '200': '恭喜你，成功领取优惠券!'
                }[i];
            },
            showCoupon() {
                return (this.status === 97 || this.status === 200);
            }
        },
        watch: {
            user_login: {
                handler(val) {
                    if (val !== '') {
                        this.requestInfo();
                    }
                },
                immediate: true
            }
        },
        methods: {
            async requestInfo() {
                const {code} = this.$route.query;
                const {data} = await this.$http.get(API.eventApi('info'), {code});
                if (data) {
                    const {status, title, banner, btn_text, btn_url, native_url, description, share_info, id, prizes, attend_type} = data;
                    Object.assign(this, {
                        code,
                        status,
                        title,
                        banner,
                        btn_text,
                        btn_url,
                        native_url,
                        description: description ? description.split('\n') : '',
                        id,
                        prizes,
                        attend_type
                    });
                    //更新分享信息
                    this.updateShare(share_info);
                    //已结束 & 开启倒计时
                    if(status === 3){
                        let t = this.timeCount;
                        this.countDown(t,()=>{
                            this.eventNotice();
                        });
                    }
                    //进行中 & 去登录/去领取
                    if (status === 2) {
                        if (!this.user_login) {
                            this.$store.state.login.isShowLogin = true;
                        } else {
                            this.requestTake();
                        }
                    }
                }
            },
            async requestTake() {
                const {code} = this;
                const {data} = await this.$http.get(API.eventApi('attend'), {code, unread_pop: 1}, false);
                if (data) {
                    this.prizes = data.concat([]);
                    this.status = 200;
                }
            },
            eventGoBtn() {
                const {btn_url, native_url, code} = this;
                this.log({
                    aid: 'go',
                    cid: 'event',
                    from: platform.isWeixin ? 'weixin' : '',
                    code
                });
                this.openUrl({path: platform.isApp ? native_url : btn_url})
            },
            eventNotice() {
                clearTimeout(this.time);
                this.openUrl({path: platform.isApp ? 'ddxq://product/home' : 'maicai:/market'})
            },
            countDown(t, fn) {
                let _that = this;
                this.time = setTimeout(() => {
                    if (t > 0) {
                        _that.countDown(t - 1, fn);
                        _that.timeCount = t - 1;
                    } else {
                        fn && fn();
                        clearTimeout(_that.time);
                    }
                }, 1000);
            }
        },
        destroyed() {
            clearTimeout(this.time);
        }
    }
</script>

