<template>
    <div class="coupon" v-if="theme === 1" :style="bottomStyle">
        <div class="coupon-left">
            <div class="coupon-price">
                <span class="price-icon">¥</span>
                <span class="price-size" :style="fontStyle">{{coupon.money}}</span>
            </div>
            <p class="coupon-desc">满{{coupon.pay_min}}可用</p>
        </div>
        <div class="coupon-right">
            <span class="round-top"></span>
            <span class="round-bottom"></span>
            <p class="coupon-name">{{coupon.name}}</p>
            <p class="coupon-time" v-if="validTime">有效期 {{validTime}}</p>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            //券信息
            coupon: {
                default: () => {},
                type: Object
            },
            //主题风格
            theme: {
                default: 1
            },
            //下间距$px
            bottom: {
                default: 5
            }
        },
        methods: {},
        computed: {
            fontStyle() {
                let {money} = this.coupon;
                let mLength = String(money).length;
                return {
                    fontSize: `${35 - (35 - 16) * ((mLength > 6 ? 6 : mLength) - 1) * 0.25}px`
                }
            },
            bottomStyle() {
                return {
                    marginBottom: `${this.bottom}px`
                }
            },
            validTime() {
                let {valid_type, valid_days, begin_time, end_time} = this.coupon;
                let time = '';
                if (valid_type === 0) {
                    time = `${begin_time}至${end_time}`
                }
                if (valid_type === 1) {
                    time = `${valid_days} 天`
                }
                return time;
            }
        }
    }
</script>

